<template>
  <v-card
    height="100%"
  >
    <div
      id="investigacion-investigadores-categoria-franjaetaria"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Categoría Investigador-docente y Franja Etaria.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('investigacion-investigadores-categoria-franjaetaria')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as investigadores/as.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Categoría
                </th>
                <th class="text-left">
                  Mayores de 65 años
                </th>
                <th class="text-left">
                  De 55 a 64 años
                </th>
                <th class="text-left">
                  De 45 a 54 años
                </th>
                <th class="text-left">
                  De 35 a 44 años
                </th>
                <th class="text-left">
                  De 25 a 34 años
                </th>
                <th class="text-left">
                  Menores de 25 años
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseCategoria(index) }}</td>
                <td>{{ item.sesentaycinco }}</td>
                <td>{{ item.sesentaycuatro }}</td>
                <td>{{ item.cincuentaycuatro }}</td>
                <td>{{ item.cuarentaycuatro }}</td>
                <td>{{ item.treintaycuatro }}</td>
                <td>{{ item.veinticinco }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['investigadoresCargos', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 130,
          right: 20,
          top: 40,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        isStacked: true,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        // colors: ['#dc3912'],
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Categoria', label: 'Cargos', type: 'string' },
          { id: 'Mayores de 65 años', label: 'Mayores de 65 años', type: 'number' },
          { id: 'De 55 a 64 años', label: 'De 55 a 64 años', type: 'number' },
          { id: 'De 45 a 54 años', label: 'De 45 a 54 años', type: 'number' },
          { id: 'De 35 a 44 años', label: 'De 35 a 44 años', type: 'number' },
          { id: 'De 25 a 34 años', label: 'De 25 a 34 años', type: 'number' },
          { id: 'Menores de 25 años', label: 'Menores de 25 años', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Profesores (Prof. Titular+Prof. Asociado+Prof. Adjunto+A+B+C)' },
              { v: this.categoriaData.prof.sesentaycinco, f: this.categoriaData.prof.sesentaycinco },
              { v: this.categoriaData.prof.sesentaycuatro, f: this.categoriaData.prof.sesentaycuatro },
              { v: this.categoriaData.prof.cincuentaycuatro, f: this.categoriaData.prof.cincuentaycuatro },
              { v: this.categoriaData.prof.cuarentaycuatro, f: this.categoriaData.prof.cuarentaycuatro },
              { v: this.categoriaData.prof.treintaycuatro, f: this.categoriaData.prof.treintaycuatro },
              { v: this.categoriaData.prof.veinticinco, f: this.categoriaData.prof.veinticinco }
            ]
          },
          {
            c: [
              { v: 0, f: 'JTP+D1' },
              { v: this.categoriaData.jtp.sesentaycinco, f: this.categoriaData.jtp.sesentaycinco },
              { v: this.categoriaData.jtp.sesentaycuatro, f: this.categoriaData.jtp.sesentaycuatro },
              { v: this.categoriaData.jtp.cincuentaycuatro, f: this.categoriaData.jtp.cincuentaycuatro },
              { v: this.categoriaData.jtp.cuarentaycuatro, f: this.categoriaData.jtp.cuarentaycuatro },
              { v: this.categoriaData.jtp.treintaycuatro, f: this.categoriaData.jtp.treintaycuatro },
              { v: this.categoriaData.jtp.veinticinco, f: this.categoriaData.jtp.veinticinco }
            ]
          },
          {
            c: [
              { v: 0, f: 'Ayudante de 1ra+D2' },
              { v: this.categoriaData.ayu.sesentaycinco, f: this.categoriaData.ayu.sesentaycinco },
              { v: this.categoriaData.ayu.sesentaycuatro, f: this.categoriaData.ayu.sesentaycuatro },
              { v: this.categoriaData.ayu.cincuentaycuatro, f: this.categoriaData.ayu.cincuentaycuatro },
              { v: this.categoriaData.ayu.cuarentaycuatro, f: this.categoriaData.ayu.cuarentaycuatro },
              { v: this.categoriaData.ayu.treintaycuatro, f: this.categoriaData.ayu.treintaycuatro },
              { v: this.categoriaData.ayu.veinticinco, f: this.categoriaData.ayu.veinticinco }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "Profesores (Prof. Titular+Prof. Asociado+Prof. Adjunto+A+B+C)"
      // "JTP+D1"
      // "Ayudante de 1ra+D2"
      // "Cat 4 CCT 366/06"
      // "Cat 5 CCT 366/06"
      // "Cat 6 CCT 366/06"
      // "Cat 7 CCT 366/06"
      return this.investigadoresCargos
        .reduce(
          (acc, cur) => {
            const nivA = new RegExp('Niv A', 'gm')
            const nivB = new RegExp('Niv B', 'gm')
            const nivC = new RegExp('Niv C', 'gm')
            const nivD1 = new RegExp('Niv D1', 'gm')
            const nivD2 = new RegExp('Niv D2', 'gm')
            const profesores = nivA.test(cur.descripcion_categoria) ||
          nivB.test(cur.descripcion_categoria) ||
          nivC.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Asociado' ||
          cur.descripcion_categoria === 'ASOCIADO SIMPLE' ||
          cur.descripcion_categoria === 'Profesor Titular' ||
          cur.descripcion_categoria === 'Profesor Adjunto' ||
          cur.descripcion_categoria === 'ADJUNTO SIMPLE'
            const d1jtp = nivD1.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Jefe Trab. Practicos' ||
          cur.descripcion_categoria === 'Jef Trab Pract NM' ||
          cur.descripcion_categoria === 'JTP SIMPLE'
            const d2ayu = nivD2.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Ayudante de 1ra.' ||
          cur.descripcion_categoria === 'AYUDANTE DE 1º SIMPL'
            const edadNum = Number(cur.edad)
            const edad =
              edadNum <= 25
                ? 'veinticinco'
                : edadNum <= 34
                  ? 'treintaycuatro'
                  : edadNum <= 44
                    ? 'cuarentaycuatro'
                    : edadNum <= 54
                      ? 'cincuentaycuatro'
                      : edadNum <= 64
                        ? 'sesentaycuatro'
                        : 'sesentaycinco'
            return profesores
              ? { ...acc, prof: { ...acc.prof, [edad]: acc.prof[edad] + 1 } }
              : d1jtp
                ? { ...acc, jtp: { ...acc.jtp, [edad]: acc.jtp[edad] + 1 } }
                : d2ayu
                  ? { ...acc, ayu: { ...acc.ayu, [edad]: acc.ayu[edad] + 1 } }
                  : acc
          },
          {
            prof: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            jtp: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            ayu: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            }
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'prof'
        ? 'Profesores (Prof. Titular+Prof. Asociado+Prof. Adjunto+A+B+C)'
        : v === 'jtp'
          ? 'JTP+D1'
          : v === 'ayu'
            ? 'Ayudante de 1ra+D2'
            : v
    }
  }
}
</script>
